body {
    margin: 0;
    font-size: 24px;

}
button {
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    background: none;
    line-height: 0;
    cursor: pointer;
}
a {
    text-decoration: none;
    cursor: pointer;
}
input:-webkit-autofill {
    -webkit-background-clip: text;
}